<template>
  <div>
    <loading :active.sync="isLoading">
      <div class="loading">
        <img src="@/assets/img/loading.png" alt="">
        <p class="loading-text">資料下載中...請稍後！</p>  
      </div>
    </loading>
    <main>
      <div class="hint">
        <div class="login-data">
          <p>帳號：{{user.id}}</p>
          <p>密碼：{{user.password}}</p>
        </div>
        <div class="status">
          <div class="success" :class="{ 'show': hint.success }">登入成功</div>
          <div class="fail" :class="{ 'show': hint.fail }">帳號或密碼有誤，請重新輸入！</div>
        </div>
      </div>

      <div class="login-block">
        <img src="@/assets/img/logo.png" alt="">
        <h3>請登入</h3>
        <form @submit.prevent="signin">
          <input class="email" type="email" pattern="[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$" placeholder="電子郵件" v-model="input.id" required/>
          <input class="password" type="password" placeholder="密碼" v-model="input.password" required/>
          <div class="button">
            <button class="submit" type="submit">送出</button>   
            <button class="reset" type="reset">清除</button>
          </div>
          <p class="copyright">© 2021 Design by Away</p>
        </form>
    </div>
    </main>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data () {
    return {
      users: [],
      user: {
        id: 'ozsu.akisik@example.com',
        password: 'vicky'
      },
      input: {
        id: '',
        password: ''
      },
      hint: {
        success: false,
        fail: false
      },
      isLoading: false
    }
  },
  methods: {
    getUsers() {
      const vm = this;
      const api = 'https://randomuser.me/api/?results=150&seed=cmoney';
      vm.isLoading = true;
      vm.$http.get(api).then((response) => {
        vm.users = response.data.results;
        vm.isLoading = false;
      })
    },
    signin() {
      const vm = this;
      let token = false;
      for (let i = 0; i < vm.users.length ; i++) {
        if(vm.users[i].email == vm.input.id && vm.users[i].login.password == vm.input.password) {
          vm.hint.success = true;
          vm.hint.fail = false;
          token = true;
          document.cookie = `login=true;max-age=${60*60*24};path=/;`
          setTimeout(() => {
            vm.hint.success = false;
            vm.$router.push('/admin');
          }, 3000);
        }
      }
      if(token == false) {
        vm.hint.success = false;
        vm.hint.fail = true;
        setTimeout(() => {
          vm.hint.fail = false;
        }, 3000);
      }
    }
  },  
  created() {
    this.getUsers();
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

main {
  height: 100vh; 
  background-color: $gray01;
  .hint {
    display: flex; 
    justify-content: space-between;
    .login-data {
      padding-top: 6px; 
      padding-left: 6px;
      p {
        margin: 0; 
        padding: 0; 
        font-size: 10px;
      }
    }
    .status {
      .success {
        background-color: $green01; 
        font-size: 10px; 
        color: $white; 
        padding: 10px;
        display: none;
      }
      .fail {
        background-color: $red01; 
        font-size: 10px; 
        color: $white; 
        padding: 10px;
        display: none;
      }
      .show {
        display: block;
      }
    }
  }
  .login-block {
    width: 280px; 
    height: fit-content; 
    display: flex; 
    justify-content: center; 
    flex-wrap: wrap; 
    position: absolute; 
    top: 50%; 
    left: 50%; 
    transform: translate(-50%,-50%);
    h3 {
      width: 100%; 
      text-align: center; 
      font-weight: normal;
    }
    form {
      .email {
        border-top-left-radius: 4px; 
        border-top-right-radius: 4px; 
        width: calc(100% - 12px); 
        font-size: 14px; 
        padding: 10px; 
        border: 1px solid $gray07;
      }
      .password {
        border-bottom-left-radius: 4px; 
        border-bottom-right-radius: 4px; 
        width: calc(100% - 12px); 
        font-size: 14px; 
        padding: 10px; 
        border: 1px solid $gray07;
      }
      .button {
        margin-top: 20px; 
        width: 100%; 
        display:flex;
        .submit {
          width: 50%; 
          background-color: $blue01; 
          color: $white; 
          border: 1px solid $blue01; 
          padding: 6px; 
          border-top-left-radius: 4px; 
          border-bottom-left-radius: 4px; 
          cursor: pointer;
        }
        .reset {
          width: 50%; 
          background-color: transparent; 
          color: $blue01; 
          border: 1px solid $blue01; 
          padding: 6px; 
          border-top-right-radius: 4px; 
          border-bottom-right-radius: 4px; 
          cursor: pointer;
        }
      }
      .copyright {
        font-size: 12px; 
        text-align: center; 
        color: $gray10; 
        margin-top: 40px;
      }
    }
  }
}

</style>



