<template>
  <div>
    <main>
      <div class="container">
        <h1>自選清單</h1>
        <TableForm page="customer" v-on:localstore="getLocalStorage"></TableForm>      
      </div>
    </main>
  </div>
</template>

<script>

import TableForm from '../TableForm';

export default {
  name: 'Customer',
  components: {
    TableForm
  },
  data () {
    return {
      getCheckUsers: JSON.parse(localStorage.getItem('checkUsers')),
    }
  },
  methods: {
    getLocalStorage() { //若自選清單中無任何內容，則轉導至會員列表頁
      const vm = this;
      vm.getCheckUsers = JSON.parse(localStorage.getItem('checkUsers'));
      if(vm.getCheckUsers.length == 0) {
        vm.$router.push('/admin');
      }
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
h1 {
  text-align: center;
}
</style>



