<template>
  <header>
    <nav>
      <div class="nav-brand">
        <a href="./"><img src="@/assets/img/logo.png" alt=""></a>
      </div>
      <div class="nav-menu">
        <router-link class="nav-link" class-active="active" to="./" v-if="isLogin">首頁</router-link>
        <router-link class="nav-link" class-active="active" to="/admin" v-if="isLogin">會員列表</router-link>
        <router-link class="nav-link" class-active="active" to="/customer" v-if="isLogin && hasCheckUsers">自選清單</router-link>
      </div>
      <div class="nav-user">
        <router-link to="/login" v-if="!isLogin"><button class="signin">登入</button></router-link>
        <router-link to="/login" v-if="isLogin"><button class="signout" @click="signout">登出</button></router-link>
      </div>
    </nav>  
  </header>
  
</template>

<script>
export default {
  name: 'Navbar',
  data () {
    return {
      isLogin: false,
      getCheckUsers: JSON.parse(localStorage.getItem('checkUsers')) || [],
      hasCheckUsers: false
    }
  },
  methods: {
    signout() {
      document.cookie = "login=false;path=/;"
      setTimeout(() => {
        this.$router.push('./');
      }, 0);
    },
    isSignin() { // 驗證登入
      const vm = this;
      if(document.cookie.split('=')[1] == 'true'){
        vm.isLogin = true;
      }
    },
    hasChecked() { // 用event bus來掌握localStorage裡面是否有選擇的users
      //初始化hasCheckUsers
      const vm = this;
      if(vm.getCheckUsers.length == 0) { 
        vm.hasCheckUsers = false;
      }
      else {
        vm.hasCheckUsers = true;
      }
      //event bus
      vm.$bus.$on('updateNav', function() { 
        vm.getCheckUsers = JSON.parse(localStorage.getItem('checkUsers'));
        if(vm.getCheckUsers.length == 0) {
          vm.hasCheckUsers = false;
        }
        else {
          vm.hasCheckUsers = true;
        }
      });
    }
  }, 
  created() {
    const vm = this;
    vm.isSignin();
    vm.hasChecked();
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

nav {
  width: 100%; 
  height: 55px; 
  background-color: $black; 
  display: flex; 
  justify-content: space-between; 
  align-items: center;
  .nav-brand {
    margin-left: 8px;
  }
  .nav-menu {
    .nav-link {
      color: $gray11; 
      padding: 0 4px; 
      font-size: 14px; 
      text-decoration: none;
    }
    .router-link-exact-active {
      color: $white;
    }
  }
  .nav-user {
    margin-right: 8px; 
    width: 145px; 
    display: flex; 
    justify-content: flex-end;
    .signin {
      cursor: pointer; 
      border: 0; 
      padding: 4px 14px; 
      border-radius: 2px;
    }
    .signout {
      cursor: pointer; 
      background-color: $blue01; 
      color: $white; 
      border: 0; 
      padding: 4px 14px; 
      border-radius: 2px;
    }
  }
}

</style>



