<template>
  <div>
    <loading :active.sync="isLoading">
      <div class="loading">
        <img src="@/assets/img/loading.png" alt="">
        <p class="loading-text">資料下載中...請稍後！</p>  
      </div>
    </loading>
    <div class="select-block">
      <div class="select-set">
        <select class="select-country" name="country" id="country" v-model="selectedCountry">
          <option value="全部國家">全部國家</option>
          <option v-for="item in country" :key="item" :value="item">{{item}}</option>
        </select>
        <select class="select-gender" name="gender" id="gender" v-model="selectedGender">
          <option value="全部性別">全部性別</option>
          <option value="female">female</option>
          <option value="male">male</option>
        </select>
      </div>
      <div class="count">
        <p v-if="selectedPeople != totalPeople">篩選結果：{{selectedPeople}}人，</p>
        <p>共：{{totalPeople}}人</p>
      </div>
    </div>

    <table>
      <thead>
        <tr>
          <th>自選清單</th>
          <th>照片</th>
          <th>帳號</th>
          <th>姓名</th>
          <th>性別</th>
          <th>年齡</th>
          <th>國籍</th>
          <th>電子郵件</th>
          <th>編輯</th>
        </tr>  
      </thead>
      <tbody>
        <tr v-for="item in filterUsers" :key="item.cell">
          <td class="align-right"><input type="checkbox" v-model="item.checked" @change="checkUser(item)"></td>
          <td class="user-img-block"><div class="user-img" :style="{backgroundImage: 'url(' + item.picture.large + ')' }"></div></td>
          <td>{{item.login.username}}</td>
          <td>{{item.name.first}} {{item.name.last}}</td>
          <td class="align-right">{{item.gender}}</td>
          <td class="align-right">{{item.dob.age}}</td>
          <td class="align-right">{{item.location.country}}</td>
          <td>{{item.email}}</td>
          <td class="align-right"><button @click="openModal(item)">編輯</button></td>
        </tr>
      </tbody>

    </table>

    <div id="modal">
      <div class="modal-mask" @click="closeModal"></div>
      <form class="modal-content" id="form" @submit.prevent="closeModal">
        <h4>編輯會員資料</h4>
        <div class="model-img-block">
          <div class="modal-img" :style="{backgroundImage: 'url(' + user.picture.large + ')' }"></div>
          <input type="file" id="customFile" class="form-control" ref="files">
        </div>
        <div class="modal-text">
          <div class="row">
            <div class="col col-2">
              <label for="name">First Name</label>
              <input class="input-normal" type="text" :value="user.name.first">  
            </div>
            <div class="col col-2">
              <label for="name">Last Name</label>
              <input class="input-normal" type="text" :value="user.name.last">  
            </div>
          </div>
          <div class="row">
            <div class="col col-1">
              <label for="birth">Birth</label>
              <input class="input-normal" type="text" :value="user.dob.date">  
            </div>
          </div>
          <div class="row">
            <div class="col col-2">
              <label for="email">E-Mail</label>
              <input class="input-normal" type="text" :value="user.email">  
            </div>
            <div class="col col-2">
              <label for="gender">Gender</label>
              <div class="option">
                <input type="radio" name="gender" :checked="user.gender == 'male'" > 
                <label for="gender">Male</label>
                <input  type="radio" name="gender" :checked="user.gender == 'female'"> 
                <label for="gender">Female</label>  
              </div>
            </div>
          </div>
          <div class="row">
            <div class="col col-2">
              <label for="country">Country</label>
              <input class="input-normal" type="text" :value="user.location.country">  
            </div>
            <div class="col col-2">
              <label for="state">State</label>
              <input class="input-normal" type="text" :value="user.location.state">  
            </div>
          </div>
          <div class="row">
            <div class="col col-2">
              <label for="city">City</label>
              <input class="input-normal" type="text" :value="user.location.city">  
            </div>
            <div class="col col-2">
              <label for="street">Street</label>
              <input class="input-normal" type="text" :value="user.location.street.name">  
            </div>
          </div>
          <div class="row">
            <div class="col col-2">
              <label for="number">Number</label>
              <input class="input-normal" type="text" :value="user.location.street.number">  
            </div>
          </div>
          <label for="coordinates">Coordinates</label>
          <div class="row">
            <div class="col col-2">
              <input class="input-normal" type="text" :value="user.location.coordinates.latitude">  
            </div>
            <div class="col col-2">                   
              <input class="input-normal" type="text" :value="user.location.coordinates.longitude">  
            </div>
          </div>
        </div>
        <div class="send-button">
          <button class="submit">確定</button>
          <button class="cancel">取消</button>
        </div>
        
      </form>
      
    </div>

    <Pagination :total-page="totalPage" v-on:page="getPage"></Pagination>

  </div>

</template>

<script>

import Pagination from './Pagination';

export default {
  name: 'TableForm',
  props: ['page'],
  components: {
    Pagination
  },
  data () {
    return {
      users: [],
      user: {
        name: {},
        dob: {},
        picture: {},
        location: {
          coordinates: {},
          street: {},
        },
      },
      usersBeChecked: [],
      country: [],
      currentCountry: '全部國家',
      currentGender: '全部性別',
      selectedUsers: [],
      selectedCountry: '全部國家',
      selectedGender: '全部性別',
      selectedPeople: 0,
      totalPeople: 0,
      totalPage: 0,
      itemsPerPage: 10,
      currentPage: 1,
      isLoading: false
    }
  },
  methods: {
    getUsers() {
      const vm = this;
      const api = 'https://randomuser.me/api/?results=150&seed=cmoney';
      vm.isLoading = true;
      vm.$http.get(api).then((response) => {
        vm.users = response.data.results;
        vm.selectedUsers = response.data.results;
        vm.selectedPeople = response.data.results.length;
        vm.totalPage = Math.ceil(response.data.results.length / vm.itemsPerPage);
        vm.totalPeople = response.data.results.length;
        
        // 從localStorage取出是否勾選
        vm.usersBeChecked = JSON.parse(localStorage.getItem('checkUsers')) || [];
        for(let i = 0; i < vm.totalPeople; i++){
          vm.users[i].checked = false;
          for(let j = 0; j < vm.usersBeChecked.length ; j++) {
            if(vm.users[i].email == vm.usersBeChecked[j].email) {
              vm.users[i].checked = true;
            }
          }
        }

        // 取出所有國家
        let countrySet = [];
        for(let i = 0; i < vm.totalPeople; i++){
          countrySet.push(vm.users[i].location.country);
        }
        vm.country = countrySet.filter(function(element, index, arr){
          return arr.indexOf(element) === index;
        });

        vm.customerSelectBlock();
      
        vm.isLoading = false;

      })
    },
    customerSelectBlock() {
      const vm = this;
      // 當在customer頁時，總人數等於被selected的人數、只顯示被selected的國家
      if(vm.page == 'customer') {
        let countrySet = [];
        vm.totalPeople = vm.usersBeChecked.length;
        for(let i = 0; i < vm.totalPeople; i++){
          countrySet.push(vm.usersBeChecked[i].location.country);
        }
        vm.country = countrySet.filter(function(element, index, arr){
          return arr.indexOf(element) === index;
        });
      }
    },
    checkUser(user) {
      const vm = this;
      if(user.checked){
        vm.usersBeChecked.push(user);
      }
      else {
        let index = vm.usersBeChecked.findIndex((element)=>{
          return element.email == user.email;
        })
        vm.usersBeChecked.splice(index,1);
        vm.customerSelectBlock();
      }
      let usersBeCheckedString = JSON.stringify(vm.usersBeChecked);
      localStorage.setItem('checkUsers', usersBeCheckedString);

      vm.$emit('localstore');
      vm.$bus.$emit('updateNav');
    },
    openModal(user) {
      this.user = Object.assign({}, user);
      document.querySelector("#modal").style.display = 'block';
    },
    closeModal() {
      document.querySelector("#modal").style.display = 'none';
    },
    keyDownEsc() {
      const vm = this;
      window.addEventListener('keydown', function(e) {
        if(e.code == 'Escape') {
          vm.closeModal();
        }
      });
    },
    getPage(val) {
      this.currentPage = val;
    },
  },
  computed: {
    filterUsers() {
      const vm = this;
      if(vm.selectedCountry == '全部國家' && vm.selectedGender == '全部性別') {
          vm.currentCountry = '全部國家';
          vm.currentGender = '全部性別';
          if(vm.page == 'admin'){
            vm.selectedUsers = vm.users;
          }
          else if(vm.page == 'customer'){
            vm.selectedUsers = vm.usersBeChecked;
          }
      }  
      else if(vm.selectedCountry != '全部國家' && vm.selectedGender == '全部性別') {
        if(vm.page == 'admin'){
          vm.selectedUsers = vm.users.filter(function(element, index, arr){
            return element.location.country == vm.selectedCountry;
          });
        }
        else if(vm.page == 'customer') {
          vm.selectedUsers = vm.usersBeChecked.filter(function(element, index, arr){
            return element.location.country == vm.selectedCountry;
          });
        }
      } 
      else if(vm.selectedCountry == '全部國家' && vm.selectedGender != '全部性別') {
        if(vm.page == 'admin'){
          vm.selectedUsers = vm.users.filter(function(element, index, arr){
            return element.gender == vm.selectedGender;
          });  
        }
        else if(vm.page == 'customer') {
          vm.selectedUsers = vm.usersBeChecked.filter(function(element, index, arr){
            return element.gender == vm.selectedGender;
          });  
        }
      }
      else if(vm.selectedCountry != '全部國家' && vm.selectedGender != '全部性別') {
        if(vm.page == 'admin'){
          vm.selectedUsers = vm.users.filter(function(element, index, arr){
            return element.location.country == vm.selectedCountry && element.gender == vm.selectedGender;
          });  
        }
        else if(vm.page == 'customer') {
          vm.selectedUsers = vm.usersBeChecked.filter(function(element, index, arr){
            return element.location.country == vm.selectedCountry && element.gender == vm.selectedGender;
          });  
        }
      }

      // 回到第1頁
      if(vm.currentCountry != vm.selectedCountry || vm.currentGender != vm.selectedGender) {
        vm.currentCountry = vm.selectedCountry;
        vm.currentGender = vm.selectedGender;
        vm.currentPage = 1;
      }

      vm.totalPage = Math.ceil(vm.selectedUsers.length / vm.itemsPerPage);
      vm.selectedPeople = vm.selectedUsers.length;
      return vm.selectedUsers.slice((vm.currentPage - 1) * vm.itemsPerPage , vm.currentPage * vm.itemsPerPage);
    },
    
  },
  created() {
    this.getUsers();
    this.keyDownEsc();
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

.select-block {
  display: flex; 
  justify-content: space-between; 
  align-items: center;
  .select-set {
    display: flex; 
    height: 32px;
    .select-country {
      background-color: $gray04; 
      padding: 8px 4px; 
      border: 0; 
      margin-right: 10px;
    }
    .select-gender {
      background-color: $gray04; 
      padding: 8px 4px; 
      border: 0;
    }
  }
  .count {
    display: flex;
    p {
      width: fit-content;
    }
  }
}

table {
  border: 1px solid $gray06;
  width: 100%; 
  border-spacing: 0;
  thead {
    background-image: linear-gradient(to bottom, $gray02 0%, $gray05 99%, $gray05 100%);
    tr {
      th {
        padding: 8px 0;
      }
    }
  }
  tbody {
    tr {
      td {
        padding: 8px 4px; 
        border-top: 1px solid $gray06;
        button {
          background-color: $red01; 
          color: $white; 
          padding: 4px 14px; 
          border: 0; 
          border-radius: 2px; 
          cursor: pointer;
        }
      }
      .align-right {
        text-align: center;
      }
      .user-img-block {
        display: flex; 
        justify-content: center;
        .user-img {
          width: 40px; 
          height: 40px; 
          background-size: cover; 
          background-position: center;
        }
      }
    }
    tr:nth-child(even) {
      background-color: $gray03;
    }
    tr:hover {
      background-color: $yellow01;
    }
  }
}

#modal {
  display: none;
  .modal-mask {
    z-index: 10; 
    position: fixed; 
    top: 0; 
    left: 0; 
    width: 100%; 
    height: 100%; 
    background-color: rgba(0,0,0,0.7);
  }
  .modal-content {
    background-color: $white;
    width: calc(650px - 24px);
    height: calc(550px - 24px);
    position: fixed;
    top: 50%;
    left: 50%; 
    transform: translate(-50%,-50%); 
    z-index: 11;
    display: flex;
    justify-content: space-between;
    align-content: flex-start;
    flex-wrap: wrap;
    border-radius: 4px;
    h4 {
      width: 100%; 
      height: fit-content; 
      margin: 0; 
      background-color: $gray08; 
      color: $white; 
      padding: 8px 12px; 
      font-weight: normal; 
      border-top-left-radius: 4px; 
      border-top-right-radius: 4px;
    }
    .model-img-block {
      width: 200px;
      margin-top: 12px;
      margin-left: 12px;
      .modal-img {
        width: 100px; 
        height: 100px; 
        margin-bottom: 12px;
        background-position: center; 
        background-size: cover;
      }  
    }
    .modal-text {
      width: calc(100% - 224px);
      margin-top: 12px;
      label {
        font-size: 14px;
      }
      .row {
        width: 100%; 
        display: flex; 
        margin-bottom: 4px;
        .col {
          .input-normal {
            width: calc(100% - 40px); 
            margin-top: 4px; 
            padding: 6px 8px; 
            border: 1px solid $gray07; 
            border-radius: 4px;
          }
          .option {
            width: calc(100% - 40px); 
            margin-top: 4px;
          }
        }
        .col-2 {
          width: 50%;
        }
        .col-1 {
          width: 100%;
        }
      }
      p {
        font-size: 12px;
        line-height: 18px;
        margin: 0;
        padding: 0;
      }
    }
    .send-button {
      width: 100%; 
      height: 28px; 
      display: flex; 
      justify-content: center; 
      margin-top: 20px;
      .submit {
        padding: 0 12px; 
        background-color: $blue01; 
        color: $white; 
        border: 0; 
        border-radius: 2px; 
        margin-right: 4px;
      }
      .cancel {
        padding: 0 12px; 
        background-color: $gray09; 
        color: $white; 
        border: 0; 
        border-radius: 2px;
      }
    }
  }
}

</style>



