<template>
  <div>
    <main>
      <loading :active.sync="isLoading">
        <div class="loading">
          <img src="@/assets/img/loading.png" alt="">
          <p class="loading-text">資料下載中...請稍後！</p>  
        </div>
      </loading>
      <div class="container">
        <h1 style="text-align: center;">前端開發人力仲介</h1>
        <div class="user-set">
          <div class="user-item" v-for="item in filterUsers" :key="item.cell" @click="openModal(item)">
            <div class="user-img" :style="{backgroundImage: 'url(' + item.picture.large + ')' }">
              <div class="user-icon user-icon-male" v-if="item.gender == 'male'">
                <i class="fas fa-male"></i>
              </div>
              <div class="user-icon user-icon-female" v-if="item.gender == 'female'">
                <i class="fas fa-female"></i>
              </div>
              <div class="user-name">{{item.name.first}} {{item.name.last}}</div>
            </div>
            <div class="user-country">{{item.location.country}}</div>
            <div class="user-city">{{item.location.city}}</div>
          </div>
        </div>

        <div id="modal">
          <div class="modal-mask" @click="closeModal"></div>
          <div class="modal-content">
            <div class="modal-img" :style="{backgroundImage: 'url(' + user.picture.large + ')' }"></div>
            <div class="modal-text">
              <p>{{user.name.first}} {{user.name.last}}</p> 
              <p>{{user.gender}}</p> 
              <p>{{user.email}}</p> 
              <p>{{user.cell}}</p>
              <p>{{user.dob.age}}</p>  
            </div>
            <iframe :src="`https://maps.google.com/?q=${user.location.coordinates.latitude},${user.location.coordinates.longitude}&output=svembed`" width="100%" height="244" style="border:0;" allowfullscreen="" loading="lazy"></iframe>
          </div>
          
        </div>

        <Pagination :total-page="totalPage" v-on:page="getPage"></Pagination>
      </div>

      

    </main>
  </div>
</template>

<script>

import Pagination from '../Pagination';

export default {
  name: 'Home',
  components: {
    Pagination
  },
  data () {
    return {
      users: [],
      user: {
        name: {},
        dob: {},
        picture: {},
        location: {
          coordinates: {}
        }
      },
      totalPage: 0,
      itemsPerPage: 20,
      currentPage: 1,
      isLoading: false
    }
  },
  methods: {
    getUsers() {
      const vm = this;
      const api = 'https://randomuser.me/api/?results=150&seed=cmoney';
      vm.isLoading = true;
      vm.$http.get(api).then((response) => {
        vm.users = response.data.results;
        vm.totalPage = Math.ceil(response.data.results.length / vm.itemsPerPage);
        vm.isLoading = false;
        console.log(vm.users);
      })
    },
    openModal(user) {
      this.user = user;
      document.querySelector("#modal").style.display = 'block';
    },
    closeModal() {
      document.querySelector("#modal").style.display = 'none';
    },
    keyDownEsc() {
      const vm = this;
      window.addEventListener('keydown', function(e) {
        if(e.code == 'Escape') {
          vm.closeModal();
        }
      });
    },
    getPage(val) {
      this.currentPage = val;
    },
  },
  computed: {
    filterUsers() {
      return this.users.slice((this.currentPage - 1) * this.itemsPerPage , this.currentPage * this.itemsPerPage);
    },
  },
  created() {
    this.getUsers();
    this.keyDownEsc();
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

.user-set {
  width: 100%; 
  display: flex; 
  flex-wrap: wrap;
  .user-item {
    width: calc((100% - 192px) / 8); 
    padding: 12px;
    .user-img {
      position: relative; 
      width: 100%; 
      padding-top: calc(100% - 20px); 
      border-radius: 3px; 
      background-size: cover; 
      background-repeat: no-repeat;
      .user-icon {
        position: absolute; 
        top: 4px; 
        left: 4px; 
        width: 18px; 
        height: 18px; 
        border-radius: 50%; 
        display: flex; 
        justify-content: center; 
        align-items: center;
        i {
          color: $white; 
          font-size: 10px;
        }
      }
      .user-icon-male {
        background-color: rgba(47,162,235,0.6); 
      }
      .user-icon-female {
        background-color: rgba(247,49,171,0.6);
      }
      .user-name {
        font-size: 12px; 
        overflow: hidden; 
        text-overflow: ellipsis; 
        white-space: nowrap; 
        padding: 4px 6px; 
        border-bottom-left-radius: 3px; 
        border-bottom-right-radius: 3px; 
        background-color: rgba(0,0,0,0.5); 
        color: $white;
      }
    }
    .user-country {
      font-size: 10px; 
      color: $gray10; 
      line-height: 14px; 
      padding-top: 4px;
    }
    .user-city {
      font-size: 10px; 
      color: $gray10; 
      line-height: 14px;
    }
  }
  .user-item:hover {
    background-color: $yellow01;
    cursor: pointer;
  }
}

#modal {
  display: none;
  .modal-mask {
    z-index: 10; 
    position: fixed; 
    top: 0; 
    left: 0; 
    width: 100%; 
    height: 100%; 
    background-color: rgba(0,0,0,0.7);
  }
  .modal-content {
    background-color: $white;
    width: calc(400px - 24px);
    height: calc(380px - 24px);
    padding: 12px;
    position: fixed;
    top: 50%;
    left: 50%; 
    transform: translate(-50%,-50%); 
    z-index: 11;
    display: flex;
    justify-content: space-between;
    align-content: space-between;
    flex-wrap: wrap;
    border-radius: 4px;
    .modal-img {
      width: 100px; 
      height: 100px; 
      background-position: center; 
      background-size: cover;
      border-radius: 4px;
    }
    .modal-text {
      width: calc(100% - 112px);
      p {
        font-size: 12px;
        line-height: 18px;
        margin: 0;
        padding: 0;
      }
    }
  }
}

</style>



