<template>
  <div class="pagination">
    <a class="previous" @click="choosePage(currentPage - 1)" :class="{ 'disabled': !hasPrePage }">
      <i class="fas fa-angle-double-left"></i>
    </a>
    <a class="page" v-for="n in totalPage" :key="n" @click="choosePage(n)" :class="{ 'current': isCurrentPage(n) }">{{n}}</a>
    <a class="next" @click="choosePage(currentPage + 1)" :class="{ 'disabled': !hasNextPage || totalPage == 1 }">
      <i class="fas fa-angle-double-right"></i>
    </a>
  </div>  
</template>

<script>
export default {
  name: 'Pagination',
  props: ['totalPage'],
  data () {
    return {
      hasPrePage: false,
      hasNextPage: true,
      currentPage: 1,
    }
  },
  methods: {
    choosePage(page) {
      if(page < 1) {
        this.currentPage = 1;  
      }
      else if (page > this.totalPage) {
        this.currentPage = this.totalPage; 
      }
      else {
        this.currentPage = page;
        window.scrollTo(0,0);
      }
      this.$emit('page',this.currentPage);
    },
    isCurrentPage(page) {
      if(page == this.currentPage){
        return true;
      }
      else {
        return false;
      }
    },
  },
  watch: {
    currentPage (){
      if(this.currentPage == 1) {
        this.hasPrePage = false;
        this.hasNextPage = true;
      }
      else if (this.currentPage == this.totalPage) {
        this.hasPrePage = true;
        this.hasNextPage = false;
      }
      else {
        this.hasPrePage = true;
        this.hasNextPage = true;
      }
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.pagination {
  display: flex; 
  justify-content: center; 
  margin: 20px 0;
  .previous {
    cursor: pointer;
    border-top-left-radius: 4px; 
    border-bottom-left-radius: 4px; 
    font-size: 14px; 
    width: 40px; 
    height: 30px; 
    border: 1px solid $gray07; 
    display: flex; 
    justify-content: center; 
    align-items: center;
  }
  .page {
    cursor: pointer; 
    font-size: 14px; 
    width: 40px; 
    height: 30px; 
    border: 1px solid $gray07; 
    display: flex; 
    justify-content: center; 
    align-items: center;
  }
  .next {
    cursor: pointer; 
    border-top-right-radius: 4px; 
    border-bottom-right-radius: 4px; 
    font-size: 14px; 
    width: 40px; 
    height: 30px; 
    border: 1px solid $gray07; 
    display: flex; 
    justify-content: center; 
    align-items: center;
  }
  .current {
    background-color: $blue01;
    color: $white;
  }
  .disabled {
    cursor: default;
    i {
      color: $gray07;  
    }
  }
}
</style>



